
import { defineComponent } from 'vue';

export default defineComponent({
  name: 'FullSizeText',
  data() {
    return {
      image: require(`../assets/img/${this.imageSource}`),
    };
  },
  props: {
    imageSource: {
      required: true,
      type: String,
    },
    altText: {
      required: true,
      type: String,
    },
    headingText: {
      required: false,
      type: String,
    },
  },
});
