    return {
      image: require(`../assets/img/${this.imageSource}`),
    };
  },
  props: {
    headingText: {
      required: true,
      type: String,
    },
    imageSource: {
      required: true,
      type: String,
    },
    teaserText: {
      required: true,
      type: String,
    },
    buttonText: {
      required: true,
      type: String,
    },
    buttonLink: {
      required: true,
      type: String,
    },
    buttonEnabled: {
      required: true,
      type: Boolean,
