
import { defineComponent } from 'vue';
import VueMarkdown from 'vue-markdown-render';

export default defineComponent({
  name: 'PageHeader',
  components: {
    VueMarkdown,
  },
  data() {
    return {
      teaser: require(`../assets/markdown/${this.teaserSource}`).default,
      image: require(`../assets/img/${this.imageSource}`),
    };
  },
  props: {
    ctaText: {
      required: true,
      type: String,
      default: 'Button Text',
    },
    ctaLink: {
      required: true,
      type: String,
      default: '#',
    },
    imageSource: {
      required: true,
      type: String,
    },
    headingText: {
      required: true,
      type: String,
    },
    teaserSource: {
      required: true,
      type: String,
    },
  },
});
