
import { defineComponent } from 'vue';
import VueMarkdown from 'vue-markdown-render';

export default defineComponent({
  name: 'FullSizeText',
  components: {
    VueMarkdown,
  },
  data() {
    return {
      text: require(`../assets/markdown/${this.textSource}`).default,
    };
  },
  props: {
    heading: {
      required: true,
      type: String,
    },
    textSource: {
      required: true,
      type: String,
    },
  },
});
