
import { defineComponent } from 'vue';

export default defineComponent({
  name: 'overlay',
  components: {
  },
  data() {
    return {
      isActive: false,
    };
  },
  methods: {
    openOverlay() {
      this.isActive = true;
    },
    closeOverlay() {
      this.isActive = false;
    },
  },
});
